.imgholder{
    width:80vw;
    margin: auto;
    height:30vh;
    border-radius: 2vh;
    
}
.img{
    width:75vw;
    height:25vh;
    object-fit: contain;
    border-radius: 5vh;
}
.nameuser{
    text-align: center;
    text-shadow: .3vh .3vh .3vh #aaa;
    box-shadow: .1vh .2vh 1vh .1vh ;
    margin: 3vh;
    margin: auto;
    padding:2vh;
    background:linear-gradient(120deg,black,var(--green-main));
    border-radius: 3vh;
}
.inputholder{
    display: flex;
    justify-content: center;
    padding: 3vh;
}
.input{
    border: none;
    border-radius: 3vh;
    height:5vh;
    width:50vw;
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    background: linear-gradient(120deg,var(--pink-main),var(--green-main));
    opacity: .7;
}
.canvas{
    min-height: 50vh;
    border-radius: 3vh;
    box-shadow: .1vh .2vh 1vh .1vh ;
    overflow-y: scroll;
    padding:2vh;
    margin:1vh;
}
.info{
    display: flex;
    color:var(--pink-main);
    justify-content: center;
    flex-direction: column;
    font-size: xx-small;

}
