@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap);
*{
  padding: 0px;
  margin:0px;
  box-sizing: border-box;
}
body{
  display: grid;
  place-items: center;
  height:100vh;
  width:100vw;
  background-color: var(--black-main);;
  --green-main:#39FF14;
  --green-primary:#008F11;
  --green-secondary:#003B00;
  --black-main: #000000;
  --pink-main:#DA14FF;
  --red-main:#ff073a;
  --yellow-main:#ccff15;
  
}
.main{
    width:98vw;
    height:98vh;
    border:.5vh solid #aaa;
    border-radius: 1vh;
    font-family: 'Orbitron', sans-serif;
    color:#ccc;
    display: grid;
    place-items: center;
}
.header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    border: .5vh solid #aaa;
    width:96vw;
    padding:1vh;
    border-radius: 1vh;
    box-shadow: .1vh .2vh 1vh .1vh ;
}
.header__logo{
    font-size: xx-large;
    text-shadow: .3vh .3vh .3vh #aaa;
}
.header__text{
    color:var(--green-main)
}
.signinform{
    display:flex;
    flex-direction: column;
    justify-content:center;
    border: .5vh solid #aaa;
    width:44vw;
    padding:2vw;
    border-radius: 1vh;
    box-shadow: .1vh .2vh 1vh .1vh ;
}
.inputemail{
    margin-bottom: 3vh;
    height:5vh;
    border: none;
    border-radius: 1vh;
    background:linear-gradient(120deg,var(--green-main),var(--pink-main));
    color:black;
    font-family: 'Orbitron', sans-serif;
}

.inputpassword{
    margin-bottom: 3vh;
    height:5vh;
    border: none;
    border-radius: 1vh;
    background:linear-gradient(120deg,var(--green-main),var(--pink-main));
    color:black;
    font-family: 'Orbitron', sans-serif;
}
::-webkit-input-placeholder { 
    color: var(--green-secondary);
    opacity: 1; 
  }
:-ms-input-placeholder { 
    color: var(--green-secondary);
    opacity: 1; 
  }
::-ms-input-placeholder { 
    color: var(--green-secondary);
    opacity: 1; 
  }
::placeholder { 
    color: var(--green-secondary);
    opacity: 1; 
  }

  .login{
      display: flex;
    text-align: center;
    margin-bottom: 3vh;
    height:5vh;
    border: none;
    border-radius: 1vh;
    background-color:var(--pink-main);
    color:black;
    font-family: 'Orbitron', sans-serif;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-weight: bold;
  }

  .button{
    display: flex;
    text-align: center;
    margin-bottom: 3vh;
    height:5vh;
    border: none;
    border-radius: 1vh;
    background-color:var(--green-main);
    color:black;
    font-family: 'Orbitron', sans-serif;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-weight: bold;
    width:30vw;
    margin: auto;
    margin-top: 7vh;
  }
  .skullogo{
      width:40vw;
      height:30vh;
      object-fit: contain;
  }
  .msg{
    color:var(--green-main);
    text-align: center;
    display: flex;
    text-align: center;
    margin-bottom: 3vh;
    height:5vh;
    border: none;
    border-radius: 1vh;
    font-family: 'Orbitron', sans-serif;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    width:30vw;
    margin: auto;
    margin-top: 7vh;
  
  }

.home{
    width:98vw;
    height:98vh;
    border:.5vh solid #aaa;
    border-radius: 1vh;
    font-family: 'Orbitron', sans-serif;
    color:#ccc;
    display: grid;
    place-items: center;
}
.logout{
    display: flex;
  text-align: center;
  height:5vh;
  border: none;
  border-radius: 1vh;
  background-color:var(--red-main);
  color:black;
  font-family: 'Orbitron', sans-serif;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-weight: bold;
}
.signout{
    display:flex;
    flex-direction: column;
    justify-content:center;
    border-bottom: .5vh solid #aaa;
    width:50vw;
    padding:.5vw;
    border-radius: 1vh;
    box-shadow: .1vh .2vh 1vh .1vh ;
}
.title{
    display: flex;
    width:96vw;
    border-radius: 1vh;
    background: linear-gradient(120deg,black,var(--green-main));
    box-shadow: .1vh .2vh 1vh .1vh ;
    margin-top: 0;
}
.titlename{
    display: flex;
    flex-direction: row;
    flex:1 1;
    align-items: center;
    padding: 1vh;
}
.span{
    display: flex;
    flex-direction: column;
    text-align: center;
    color: #ccc;
    text-shadow: .3vh .3vh .3vh black;
}
.name{
    text-transform: capitalize;
    margin: .5vh;
    font-size: larger;
    letter-spacing: .2vh;
}
.lastseen{
    color: black;
    text-shadow: .1vh .3vh .3vh #ccc;
}
.avatar{
    margin-right: 2vw;
    display: inline;
    width:12vh!important;
    height:12vh!important;
    border: solid 1vh #aaa;
}
.avatar:hover{
    border: solid 1vh var(--green-main);
}
.burger{
    display: flex;
    align-items: center;
}
.burgericon{
    font-size: 12vh!important;
    color: black;
    font-weight: bolder;
}
.burgericon:hover{
    color:#aaa;
}
.container{
    display: flex;
    flex-direction: row;
}
.inputavatar{
    display:none;
}

.sidebarcontainer{
    display: flex;
    flex-direction: column;
    max-height: 58vh;
    overflow-y: scroll;
    border-radius: 1vh;
    box-shadow: .1vh .2vh 1vh .1vh ;
    border: .5vh solid #aaa;
    width:96vw;
}
.sidebar{
    border-radius: 1vh;
    background: linear-gradient(120deg,black,var(--pink-main)); 
    border-radius: 1vh;
    box-shadow: .1vh .2vh 1vh .1vh #ccc;
    margin:.7vh;
}
.titlenamesidebar:hover {
    background-color:#ccc;
    opacity:.15;
    cursor: pointer;
}
.titlenamesidebar{
    display: flex;
    flex-direction: row;
    flex:1 1;
    align-items: center;
    padding: 1vh;
}
.spansidebar{
    display: flex;
    flex-direction: column;
    text-align: center;
    color: gold;
    text-shadow: .5vh .3vh .3vh black;
}
.namesidebar{
    text-transform: capitalize;
    margin: .5vh;
    font-size: x-small;
    letter-spacing: .2vh;
}
.lastseensidebar{
    font-size: xx-small;
    color: var(--red-main);
}
.avatarsidebar{
    margin-right: 2vw;
    display: inline;
    width:8vh!important;
    height:8vh!important;
}
.imgholder{
    width:80vw;
    margin: auto;
    height:30vh;
    border-radius: 2vh;
    
}
.img{
    width:75vw;
    height:25vh;
    object-fit: contain;
    border-radius: 5vh;
}
.nameuser{
    text-align: center;
    text-shadow: .3vh .3vh .3vh #aaa;
    box-shadow: .1vh .2vh 1vh .1vh ;
    margin: 3vh;
    margin: auto;
    padding:2vh;
    background:linear-gradient(120deg,black,var(--green-main));
    border-radius: 3vh;
}
.inputholder{
    display: flex;
    justify-content: center;
    padding: 3vh;
}
.input{
    border: none;
    border-radius: 3vh;
    height:5vh;
    width:50vw;
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    background: linear-gradient(120deg,var(--pink-main),var(--green-main));
    opacity: .7;
}
.canvas{
    min-height: 50vh;
    border-radius: 3vh;
    box-shadow: .1vh .2vh 1vh .1vh ;
    overflow-y: scroll;
    padding:2vh;
    margin:1vh;
}
.info{
    display: flex;
    color:var(--pink-main);
    justify-content: center;
    flex-direction: column;
    font-size: xx-small;

}

.body{
    display: flex;
    color: var(--green-main);
    background:linear-gradient(120deg,black,#aaa);
    padding:2vh;
}
.answerbody{
    display: flex;
    color: var(--pink-main);
    background:linear-gradient(120deg,black,#aaa);
    padding:2vh;
    text-align: end;
}
.skul{
    border: .5vh solid var(--green-main);
    padding:5vh;
    border-radius: 1vh;
    background: linear-gradient(120deg,black,#aaa);
}
.skullogo{
 width:25vw;
 height: 25vh;
}
.end{
    color:var(--pink-main);
    font-size: x-large;
}
